.Cell{
  float:left;
  border: 1px solid white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #cce8ff;
}
.Cell input{
  height: 85%;
  width: 85%;
}
.Cell select{
  height: 100%;
  width: 100%;
  padding-left: .3em;
  font-size: 2.3em;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
.Cell select.numbers:disabled{
  font-weight: bold;
  font-size: 2.4em;
  background-color: #e5e5e5;
}
.Cell select.icons:disabled{
  background-color: #e5e5e5;
}
.Cell select.icons{
  padding-top: .06em;
  padding-left: .1em;
}


